ductRow td.image img{max-width:150px;margin:0 auto}.ProductRow .ui.table:not(.unstackable) tr{box-shadow:none !important}}@media only screen and (max-width: 600px){.ProductRow table,.ProductRow .ui.table{text-align:center}.ProductRow td.productTextCol{max-width:100%}.ProductRow .RowButtons{width:100%;height:55px;margin-top:0}.ProductRow .RowButtons .ui.list{text-align:center;width:auto;display:inline-block}}

